/* Common styles for Product component */
.product {
  background: #fff;
  padding: 10px;
  position: relative;
  border-radius: 8px;
  box-shadow: rgb(3 0 71 / 9%) 0px 1px 3px;
  margin: 10px;
  overflow: hidden;
}

.product img {
  width: 100%;
  height: auto;
  object-fit: cover;
  cursor: pointer;
}

.product-details {
  padding: 10px;
}

.product-col {
  padding-left: 8px;
  padding-right: 8px;
  margin-bottom: 16px;
}

.discount {
  position: absolute;
  top: 0;
  left: 0;
  background: #0f3460;
  padding: 3px 10px;
  font-size: 12px;
  border-radius: 50px;
  color: #fff;
  margin: 10px;
}

.product h3 {
  margin: 10px 0;
  font-weight: 500;
  font-size: 17px;
  cursor: pointer;
}

.product .rate i {
  font-size: 15px;
  color: #ffcd4e;
  margin: 5px 5px 5px 0;
}

.product .price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: black;
}

.price h4 {
  margin: 20px 0 10px;
}

.price button {
  background: none;
  color: #0f3460;
  font-size: 20px;
  transition: 0.5s;
  border: 1px solid rgb(3 0 71 / 9%);
  width: 35px;
  height: 35px;
  border-radius: 5px;
}

.price button:hover {
  cursor: pointer;
  background-color: #0f3460;
  color: #fff;
}

button.add {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  border-radius: 50%;
}

button.add.add-to-cart-full {
  background-color: #0F3460;
  color: white;
  border: none;
  /* padding: 50px 70px; */
  width: 100px;
  height: 50px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

button.add.add-to-cart-full:hover {
  background-color: #072a48;
}

.product-like {
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px;
  opacity: 0;
  transition: 0.5s;
}

.product-like label {
  background-color: #0f3460;
  padding: 1px 10px;
  font-size: 12px;
  color: white;
  border-radius: 50px;
}

.product-like ion-icon {
  font-size: 20px;
  margin: 10px 3px;
  cursor: pointer;
}

.product:hover .product-like {
  opacity: 1;
}

@media (max-width: 768px) {
  .product {
    margin: 5px;
    padding: 8px;
    border-radius: 6px;
  }

  .product img,
  .product-image {
    height: 130px; /* reduce image height */
  }

  .product h3 {
    font-size: 14px;
  }

  .product-description {
    font-size: 12px;
  }

  .price h4 {
    font-size: 14px;
    margin: 10px 0;
  }

  .price button {
    width: 30px;
    height: 30px;
    font-size: 16px;
  }

  .button.add.add-to-cart-full {
    width: 80px;
    height: 40px;
    font-size: 12px;
  }

  .product-button {
    padding: 8px 16px;
    font-size: 12px;
  }

  #popular-packages .extra-details li {
    font-size: 12px;
  }
}

/* Specific styles for Popular Packages section */
#popular-packages .product {
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

#popular-packages .product:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.02);
}

#popular-packages .product-image-container {
  position: relative;
  width: 100%;
  height: 150px; /* Ensure this matches the height of the image */
  overflow: hidden;
}

#popular-packages .product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

#popular-packages .hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

#popular-packages .product-image-container:hover .hover-overlay {
  opacity: 1;
}

#popular-packages .hover-overlay h2 {
  font-size: 14px; /* Adjust font size as needed */
  margin-bottom: 10px; /* Optional: Add margin for spacing */
}

#popular-packages .hover-overlay ul {
  padding: 0;
  list-style: none;
  font-size: 11px; /* Adjust font size for overlayDetails */
}

#popular-packages .hover-overlay li {
  margin-bottom: 5px; /* Optional: Add margin for spacing between items */
}

#popular-packages .product-description {
  font-size: 18px; /* Adjust as needed */
  color: #555;
  margin-top: 10px;
  font-size: 13px; /* Adjust font size as needed */
  font-weight: 700;
}

#popular-packages .product-buttons {
  display: flex;
  justify-content: flex-start; /* Align buttons to the start */
  align-items: center; /* Center the buttons vertically */
  margin-top: 10px;
}

#popular-packages .product-button {
  background-color: #0F3460;
  border: none;
  color: white;
  padding: 10px 20px; /* Ensure enough padding */
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

#popular-packages .product-button:hover {
  background-color: #072a48;
  color: white;
}

#popular-packages .extra-details {
  padding: 10px 15px;
  background-color: #f9f9f9;
}

#popular-packages .extra-details ul {
  list-style: none;
  padding: 0;
}

#popular-packages .extra-details li {
  font-size: 14px;
  margin-bottom: 5px;
  color: #555;
}

#popular-packages .extra-details i {
  color: #0F3460;
  margin-right: 5px;
}
